<template>
  <div class="hello">
    <van-row type="flex" justify="center" style="height:100vh;">
      <van-col span="24">
        <van-nav-bar
            title="图片集"
            left-text="返回"
            left-arrow
            @click-left="onClickBack"
        />
        <van-grid :column-num="3">
            <van-grid-item
                v-for="(value, index) in images"
                :key="index"
                @click="onClick($event, index)"
            >
                <van-image width="27vw" height="27vh" fit="contain" :src="value"/>
            </van-grid-item>
        </van-grid>
      </van-col>
    </van-row>
  </div>
</template>

<script>
import { Toast } from 'vant';

export default {
  name: 'ComGrid',
  props: ['imageIndex'],
  data(){
    return {
      images: [],
    }
  },

  methods: {
    onClick($event, index){
        let self = this;
        window.console.log(index);
      window.cwData.$axios.get('/index.php?s=index/index/getDetail.html',{       // 还可以直接把参数拼接在url后边
        params:{
            code: this.imageIndex
        }
      }).then(function(res){
          let result = res.data;
          window.console.log(result);
          if(result.code === 1){
            window.cwData.detail.$prefix = result.data.prefix;
            window.cwData.detail.$count = result.data.count;
            self.$router.push('/com-detail');
          }else{
            Toast(result.msg);   
          }
      }).catch(function (error) {
          window.console.log(error);
      });
    },

    onClickBack(){
        this.$router.push('/com-hello/' + this.imageIndex);
    }
  },

  mounted(){    
      window.console.log("获取数据" + this.imageIndex);
    for(let i = 0;i < window.cwData.$imgs.length; i++){
        this.images.push(window.cwData.$imgBase + window.cwData.$imgs[i]['url']);
    }
  }
}
</script>

